import BbwzData from "../data/BbwzData";
import BbwzOnlinePlayerItem from "../component/BbwzOnlinePlayerItem";
import BbwzConstDefine from "../data/BbwzConstDefine";

const {ccclass, property} = cc._decorator;

/**
 * 弹窗 在线玩家界面
 */
@ccclass
export default class BbwzOnlinePlayerPop extends cc.Component {
    public loadingKey:string = "OnlinePlayerPop";
    private maskNode: cc.Node;
    private contentNode: cc.Node;
    private animComp: any;
    private scrollViewCarmack: any;       // 卡马克卷轴组件
    private scrollView:cc.ScrollView = null;
    // 滚动容器内的子节点（范例：用于生成多个）
    private itemNode:cc.Node = null;
    

    protected onLoad(){
        this.node.setContentSize(cc.Canvas.instance.node.getContentSize());
        this.maskNode = cc.find("mask", this.node);
        this.contentNode = cc.find("content", this.node);
        this.animComp = Global.UIHelper.addAnimComp(this.node, this.contentNode, this.maskNode);

        BbwzConstDefine.addCommonClick(this.contentNode, "button_close", this.onCloseClick, this);
        BbwzConstDefine.addCommonClick(this.node, "mask", this.onCloseClick, this,cc.Button.Transition.NONE);

        this.scrollView = cc.find("scrollview", this.contentNode).getComponent(cc.ScrollView);
        this.itemNode = cc.find("view/content/itemNode", this.scrollView.node);
        this.scrollViewCarmack = Global.UIHelper.addScrollViewCarmackComp(this.scrollView.node, this.itemNode, 5, 5, this, this.item_setter);
    }
    
    protected onEnable(){
        this.animComp.doPopupOpenAnim(()=>{
            this.scrollView.scrollToTop();
        });
        this.scrollViewCarmack.clearView();
    }

    protected onDisable(){
        Global.Event.event(GlobalEvent.HIDE_NET_WAITING, this.loadingKey);
    }

    /**
     * 子节点更新函数
     * @param itemNode 
     * @param index 
     */
    private item_setter(itemNode:cc.Node, index:number){
        let data = this.scrollViewCarmack.allDatas[index];
        let itemComponent:BbwzOnlinePlayerItem = Global.UIHelper.safeGetComponent(itemNode, "", BbwzOnlinePlayerItem);
        itemComponent.updateUI(data, index);
    }

    public updateAfterGetData(){
        this.updateUI();
    }

    public updateUI(){
        let datas = BbwzData.instance.onLinePlayerList;
        if(datas.length > 0){
            Global.Event.event(GlobalEvent.HIDE_NET_WAITING, this.loadingKey);
            
            for (let index = 0; index < datas.length; index++) {
                const data = datas[index];
                data.index = index;
            }
            this.scrollViewCarmack.allDatas = datas;
            this.scrollViewCarmack.updateView();
        }
    }

    private onCloseClick(){
        BbwzConstDefine.playBtnSound();
        this.animComp.doPopupCloseAnim(()=>{
            this.node.active = false;
        });
    }
}